//Functions

document.getElementById("undo").onclick = 
function undoAdd() {
  firebase.database().ref('members/' + lastId).set(null);
}

document.getElementById("add").onclick = 
function addMember() {
  var newName = document.getElementById("name").value;
  var newEmail = document.getElementById("email").value;
  var selectTeam = document.getElementById("team");
  var newTeamNum = selectTeam.options[selectTeam.selectedIndex].text;
  var newTeam = ""
  if (newTeamNum == "1") newTeam = "One";
  else if (newTeamNum == "2") newTeam = "Two";
  else if (newTeamNum == "3") newTeam = "Three";
  var selectedPosition = document.getElementById("position");
  var newPosition = selectedPosition.options[selectedPosition.selectedIndex].text;
  var id = Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
  firebase.database().ref('members/' + id).set({
    name: newName,
    email: newEmail,
    position: newPosition,
    team: newTeam,
    id: id
  })

  lastId = id;
}

document.getElementById("hide").onclick = 
function hideData() {
  document.getElementById("dts").style.visibility = "hidden";
}

document.getElementById("showByPosi").onclick = 
function byPosition() {
  document.getElementById("dts").style.visibility = "visible";
  document.getElementById("dts").innerText = "";
  document.getElementById("dts").innerHTML += "SETTER" + "<br><br>";
    for (let x in dtJson) {
      if (dtJson[x]['position'] == "Setter")
      document.getElementById("dts").innerHTML += dtJson[x]['name'] + " : " + dtJson[x]['team'] + ", " + dtJson[x]['email'] + "<br><br>";
    }

    document.getElementById("dts").innerHTML += "PASSER" + "<br><br>";
    for (let x in dtJson) {
      if (dtJson[x]['position'] == "Passer")
      document.getElementById("dts").innerHTML += dtJson[x]['name'] + " : " + dtJson[x]['team'] + ", " + dtJson[x]['email'] + "<br><br>";
    }

    document.getElementById("dts").innerHTML += "HITTER" + "<br><br>";
    for (let x in dtJson) {
      if (dtJson[x]['position'] == "Hitter")
      document.getElementById("dts").innerHTML += dtJson[x]['name'] + " : " + dtJson[x]['team'] + ", " + dtJson[x]['email'] + "<br><br>";
    }
}

document.getElementById("showByTeam").onclick = 
function byTeam() {
  document.getElementById("dts").style.visibility = "visible";
  document.getElementById("dts").innerText = "";

    document.getElementById("dts").innerHTML += "TEAM 1" + "<br><br>";
    for (let x in dtJson) {
      if (dtJson[x]['team'] == "One")
      document.getElementById("dts").innerHTML += dtJson[x]['name'] + " : " + dtJson[x]['position'] + ", " + dtJson[x]['email'] + "<br><br>";
    }

    document.getElementById("dts").innerHTML += "TEAM 2" + "<br><br>";
    for (let x in dtJson) {
      if (dtJson[x]['team'] == "Two")
      document.getElementById("dts").innerHTML += dtJson[x]['name'] + " : " + dtJson[x]['position'] + ", " + dtJson[x]['email'] + "<br><br>";
    }

    document.getElementById("dts").innerHTML += "TEAM 3" + "<br><br>";
    for (let x in dtJson) {
      if (dtJson[x]['team'] == "Three")
      document.getElementById("dts").innerHTML += dtJson[x]['name'] + " : " + dtJson[x]['position'] + ", " + dtJson[x]['email'] + "<br><br>";
    }
}